import { queryDB } from "@/lib/dbUtils";
import { fetchData } from "../../../../../../utils/api";

export async function GET(req, { params }) {
  try {
    const { userId } = params;
    if (!userId) {
      return Response.json({ error: "Usuario no especificado" }, { status: 400 });
    }
    const [users] = await queryDB(
      `SELECT u.userName, u.role, u.address, u.phone, u.bought_sold FROM users u, relaciones r, sucursales s WHERE u.user_id = r.user_child_id AND r.user_parent_id = 7 AND s.sucursal_id = r.sucursal_id;
    `,
      [userId]
    );

    return Response.json(users);
  } catch (error) {
    return Response.json({ error: error.message }, { status: 500 });
  }
}